<template>
  <div v-if="currentGroup">
  <h1 class="title">{{ currentGroup.name }}</h1>
  <h3 class="caption mt-2">{{ currentGroup.description }}</h3>
  <h1 v-bind:class="{ 'green--text': groupTotal >= 0, 'red--text': groupTotal < 0 }">${{ groupTotal }}</h1>
  <personChip
    v-for="member in members"
    :key="member.name"
    :person="member"
    class="mt-4 mb-3 mr-3">
  </personChip>
  <v-data-table
  :headers="headers"
  :items="items"
  hide-actions
  class="elevation-1">
  <template slot="items" slot-scope="props">
    <td class="text-xs-left">{{ props.item.user.username }}</td>
    <td class="text-xs-left">{{ props.item.amount }}</td>
    <td class="text-xs-left">{{ props.item.description }}</td>
    <td class="text-xs-left">{{ props.item.due }}</td>
    <td class="text-xs-left">
      <v-btn 
        v-if="props.item.payee == currentUser.uid" 
        @click.native="deletePaymentFlag(props.item.pid)" 
        flat icon color="red lighten-2">
        <v-icon>delete</v-icon>
      </v-btn>
    </td>
  </template>
</v-data-table>
  </div>
</template>

<script>
import personChip from '@/components/PersonChip';

function add(a, b) {
    return a + b;
}

export default {
  data() {
    return {
      headers: [
        { text: 'Requested By', align: 'left', value: 'username', sortable: false },
        { text: 'Amount', value: 'amount' },
        { text: 'Description', value: 'description', sortable: false },
        { text: 'Due', value: 'due' },
        { text: 'Actions', sortable: false, value: 'actions' },
      ],
    };
  },
  computed: {
    currentUser() {
      return this.$store.state.currentUser
    },
    currentGroup() {
      return this.$store.state.currentGroup
    },
    members() {
      return this.$store.state.members
    },
    items() {
      return this.$store.state.paymentFlags
    },
    groupTotal: function() {
      if (!this.items) {
        return 0
      }
      var amounts = []
      for (var i in this.items) {
        if (this.items[i].payee == this.currentUser.uid) {
          amounts.push(parseInt(this.items[i].amount))
        }
        else {
          amounts.push(-parseInt(this.items[i].amount))
        }
      }
      return amounts.reduce(add, 0)
    },
  },
  methods: {
    deletePaymentFlag: function (pid) {
      this.$store.dispatch('DELETE_PAYMENT_FLAG', pid)
    },
  },
  components: {
    personChip,
  },
};
</script>



