<template>
  <v-dialog v-model="show" persistent max-width="450">
    <v-card>
      <v-card-title class="headline">Request Payment</v-card-title>
        <v-card-text>
          <v-subheader>Amount</v-subheader>
          <v-layout row>
            <v-flex xs9>
              <v-card-text>
                <v-slider v-model="value" thumb-label step="0.50" max="150" ticks></v-slider>
              </v-card-text>
            </v-flex>
            <v-flex xs3>
              <v-text-field
                v-model="value"
                prefix="$">
              </v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12>
              <v-card-text>
                <v-menu
                  ref="dateMenu"
                  lazy
                  :close-on-content-click="false"
                  v-model="dateMenu"
                  transition="scale-transition"
                  offset-y
                  full-width
                  :nudge-right="40"
                  min-width="290px"
                  :return-value.sync="dueDate">
                  <v-text-field
                    slot="activator"
                    label="Select Due Date"
                    v-model="dueDate"
                    prepend-icon="event"
                    readonly>
                  </v-text-field>
                  <v-date-picker v-model="dueDate" landscape scrollable>
                    <v-spacer></v-spacer>
                    <v-btn flat color="primary" @click="dateMenu = false">Cancel</v-btn>
                    <v-btn flat color="primary" @click="$refs.dateMenu.save(dueDate)">OK</v-btn>
                  </v-date-picker>
                </v-menu>
              </v-card-text>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12>
              <v-card-text>
                <v-text-field
                  label="Description"
                  multi-line
                  rows=2
                  :rules="[(v) => v.length <= 200 || 'Max 200 characters']"
                  :counter="200"
                  v-model="paymentDesc"
                  required>
                </v-text-field>
              </v-card-text>
            </v-flex>
          </v-layout>
          <v-subheader>Who</v-subheader>
          <v-card-text>
            <p class="caption">Leave blank for entire group or add who you wish to request from.</p>
          <v-select
            :items="members"
            label="Members"
            autocomplete></v-select>
          </v-card-text>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            flat="flat"
            @click.native="$emit('close')">
              Cancel
          </v-btn>
          <v-btn
            color="green darken-1"
            flat="flat"
            @click.native="raisepayment">
              Submit
          </v-btn>
        </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  data() {
    return {
      value: 10,
      recurring: false,
      dueDate: null,
      dateMenu: false,
      paymentDesc: ""
    };
  },
  computed: {
    members() {
      var members = []
      for (var i in this.$store.state.members) {
        members.push(this.$store.state.members[i].username)
      }
      return members
    },
  },
  methods: {
    raisepayment: function () {
      var params = {amount: this.value, due: this.dueDate, description: this.paymentDesc}
      this.$store.dispatch('CREATE_PAYMENT_FLAG', params)
      this.$emit('close');
    }
  },
  props: [
    'show',
  ],
};
</script>



